	module.exports = {"data":{"markdownRemark":{"html":"<p>凌晨继续研究《MS_DOS6.0》的书，对一些新的功能有所把握。用电脑，已经改变了我写文章的方式，同时也改变了我的思维方式。主要是出来的概念不一样，用笔写的时候出来的是笔划，用电脑的拼音输入方法，出来的首先是声音。不知用别的方式的输入的人会发生什么样的变化。以后社会上会不会形成不同的阶层，以由电脑造就的不同思维方式来划分。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0062.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0062.md","frontmatter":{"title":"3月18日 星期五"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0062.md absPath of file >>> MarkdownRemark"}}

